@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url('SEGA.woff') format('woff');
}

.header h1{
	font-family: 'SEGA LOGO FONT';
	color: white;
	font-weight: 400;
	letter-spacing: 0.5rem;
	font-size: 4em;
	margin-bottom: 20px;
	text-align: center;
}

.loadText h1{
	font-family: 'SEGA LOGO FONT';
	color: white;
	font-weight: 400;
	letter-spacing: 4rem;
	font-size: 5rem;
	text-align:center;
	margin:30vh auto ;

}



@media screen and (max-width: 900px) {
.header h1 {
	font-weight: 200;
	letter-spacing: 0.1rem;
	font-size: 2em;
	display:block;
	text-align: center;
	vertical-align: middle;

  }
 .searchbox{
 	display:block;
 }
.container{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	}
.loadText h1{
	font-weight: 300;
	letter-spacing: 1rem;
	font-size: 2rem;
}
}
@media screen and (max-width: 480px) {
.header h1 {
	font-weight: 200;
	letter-spacing: 0.1rem;
	font-size: 2.2em;
	display:block;
	text-align: center;
	

  }
 .searchbox{
 	display:block;
 }
.container{
	display: flex;
	flex-direction: column;
	
	}
}

